@use "base/variables" as v;
@use "base/mixins" as m;

.portafolio{
    .descripcion{
        line-height: 1.5;
    }
}


.swiper {
    position: relative;

    .swiper-pagination {
        .swiper-pagination-bullet {
            width: 1rem;
            height: 1rem;
            background-color: v.$amarillo;
        }
    }
}

.not-found {
    display: flex;
    background-color: v.$negro;
    padding: 2rem;

    h1 {
        font-size: 3rem;
        text-transform: uppercase;
    }
}

.swiper-wrapper{
    margin-top: 2rem;

    .swiper-slide {
        display: flex;
        flex-direction: column; 
        height: auto;

        @include m.tablet{
            @include m.grid(2,2fr);
            align-items: center;
        }
        
        
        .imagen{
            flex: 1;
            display: block;
            object-fit: contain;
        }
        
        .contenido{
            flex: 1;
            text-align: center;
            padding: 1rem;

            a{
                font-weight: v.$black;
                color: v.$azul;
            }

            p{
                font-weight: v.$bold;
                color: v.$amarillo;

                span{
                    font-weight: v.$regular;
                    color: v.$negro;
                }
            }
        }
    }
}

.hero {
    display: flex;
    flex-direction: column;
    transition: all 0.5s ease-in-out;

    @include m.tablet {
        @include m.grid(2, 1fr);
        align-items: center;
    }

    .contenido {
        flex: 1;
        display: flex;
        padding: 2rem;
        flex-direction: column;
        gap: 2rem;
        transition: all 0.5s ease-in-out;
        z-index: 100;

        @include m.desktop {
            padding: 10rem;
        }
        p {
            margin: 0;
            line-height: 1.5;
            text-align: initial;
            span {
                font-size: 2.4rem;
                font-weight: bold;
                color: v.$amarillo;
            }
        }

        .boton__cv {
            @include m.boton(v.$amarillo, v.$blanco);
        }
    }

    .imagen {
        position: relative;
        transition: transform 0.5s ease-in-out;

        &:hover {
            img {
                transform: translateY(2rem);
                transition: transform 0.5s ease-in-out;
            }
        }   
        .img {
            position: absolute;
            top: 4rem;
            right: 4rem;
            bottom: 4rem;
            left: 4rem;
            display: flex;
            z-index: 100;

        }
    }
}