@use "base/variables" as v;
@use "base/mixins" as m;

.centrado__loader {
    height: 70vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    opacity: 1;
    z-index: 100;
    transition: all 1s ease-in-out;

    &.ocultar{
        visibility: hidden;
        opacity: 0;
        display: none;
    }
}

.lds-ripple {
    display: inline-block;
    position: relative;
    width: 8rem;
    height: 8rem;
}
.lds-ripple div {
    position: absolute;
    opacity: 1;
    border-radius: 50%;
    animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
    animation-delay: -0.5s;
}
@keyframes lds-ripple {
    0% {
        border: 0.4rem solid v.$amarillo;
        top: 3.6rem;
        left: 3.6rem;
        width: 0;
        height: 0;
        opacity: 0;
    }
    4.9% {
        border: 0.4rem solid v.$azul;
        top: 6.6rem;
        left: 3.6rem;
        width: 8.2rem;
        height: 3.2rem;
        opacity: 0;
    }
    5% {
        border: 0.4rem solid v.$amarillo;
        top: 3.6rem;
        left: 3.6rem;
        width: 0;
        height: 0;
        opacity: 1;
    }
    100% {
        border: 0.4rem solid v.$azul;
        top: 0px;
        left: 0px;
        width: 7.2rem;
        height: 7.2rem;
        opacity: 0;
    }
}
