@use "variables" as v;

.darkMode {
    background-color: v.$negro;

    .header {
        background-color: v.$negro;
    }

    p {
        color: v.$blanco;
    }

    .cuadro {
        background-color: #1a1b15;
        box-shadow: inset -5px 5px 10px #161611, inset 5px -5px 10px #1e2019;
        border: none;
    }

    .fecha {
        color: v.$blanco;
    }

    .cuadro__estudios {
        .estudio {
            span {
                color: v.$blanco;
            }
        }
    }

    .cuadro__referencias {
        .contacto {
            span {
                color: v.$blanco;
            }
        }
    }
}
