@use "base/variables" as v;
@use "base/mixins" as m;

.header {
    position: sticky;
    top: 0rem;
    border-bottom: 0.1rem solid v.$amarillo;
    z-index: 2;
    transition: all .5s ease-in-out;

    .nombre-pagina {
        display: flex;
        justify-content: center;
        margin: 0;
        margin-top: 2rem;
        
        @include m.tablet {
            margin: 0;
            padding: 2rem;
        }
        
        a {
            font-size: 2.5rem;
            color: v.$amarillo;
            line-height: 1.5rem;
            font-weight: v.$black;
            transition: all 0.5s ease;
            
            &:hover {
                color: v.$amarillo;
                transform: scale(1.2);
                transform-origin: 20% 100%;
            }

            span{
                font-weight: v.$black;
                color: v.$azul;
                font-size: 1.5rem;
                letter-spacing: .2rem;

            }
        }
    }

    .menu {
        color: v.$azul;
        text-align: center;

        @include m.tablet {
            display: none;
        }
    }

    #menu-mobile {
        font-size: 2.2rem;
        cursor: pointer;
        transition: all 0.3s ease-in-out;

        &:hover {
            transform: scale(1.1);
            color: v.$amarillo;
        }
    }

    .nav {
        display: none;

        @include m.tablet {
            display: flex;
            justify-content: center;
            align-items: center;
        }

        a {
            width: 100%;
            text-align: center;
            color: v.$azul;
            font-weight: bold;
            padding: 2rem;
            transition: all 0.5s ease-in-out;

            &:hover {
                color: v.$blanco;
                background-color: v.$amarillo;
            }
            &.activo {
                background-color: darken(v.$amarillo, 5%);
                color: v.$blanco;
            }
        }

        &.mostrar {
            display: flex;
            flex-direction: column;
            text-transform: uppercase;
        }
    }
}
