/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  position: relative;
  min-height: 100%;
  font-size: 1.6rem;
  display: flex;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
  overflow-x: none;
  transition: all 0.5s ease-in-out;
}

*, *:before, *:after {
  box-sizing: inherit;
}

