}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
  color: #F0A500;
}

h1::selection {
  color: #FFFFFF;
  background-color: #F0A500;
}

h2::selection {
  color: #FFFFFF;
  background-color: #F0A500;
}

h3::selection {
  color: #FFFFFF;
  background-color: #F0A500;
}

h4::selection {
  color: #FFFFFF;
  background-color: #F0A500;
}

h5::selection {
  color: #FFFFFF;
  background-color: #F0A500;
}

h6::selection {
  color: #FFFFFF;
  background-color: #F0A500;
}

p::selection {
  color: #FFFFFF;
  background-color: #F0A500;
}

span::selection {
