@media (min-width: 768px) {
  .not-found .title {
    font-size: 10rem;
  }
}

.header {
  position: sticky;
  top: 0rem;
  border-bottom: 0.1rem solid #F0A500;
  z-index: 2;
  transition: all 0.5s ease-in-out;
}
.header .nombre-pagina {
  display: flex;
  justify-content: center;
  margin: 0;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .header .nombre-pagina {
    margin: 0;
    padding: 2rem;
  }
}
.header .nombre-pagina a {
  font-size: 2.5rem;
  color: #F0A500;
  line-height: 1.5rem;
  font-weight: 900;
  transition: all 0.5s ease;
}
.header .nombre-pagina a:hover {
  color: #F0A500;
  transform: scale(1.2);
  transform-origin: 20% 100%;
}
.header .nombre-pagina a span {
  font-weight: 900;
  color: #537A97;
  font-size: 1.5rem;
  letter-spacing: 0.2rem;
}
.header .menu {
  color: #537A97;
  text-align: center;
}
@media (min-width: 768px) {
  .header .menu {
    display: none;
  }
}
.header #menu-mobile {
  font-size: 2.2rem;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.header #menu-mobile:hover {
  transform: scale(1.1);
  color: #F0A500;
}
.header .nav {
  display: none;
}
@media (min-width: 768px) {
  .header .nav {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.header .nav a {
  width: 100%;
  text-align: center;
  color: #537A97;
  font-weight: bold;
  padding: 2rem;
  transition: all 0.5s ease-in-out;
}
.header .nav a:hover {
  color: #FFFFFF;
  background-color: #F0A500;
}
.header .nav a.activo {
  background-color: #d79300;
  color: #FFFFFF;
}
.header .nav.mostrar {
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
}

.dark {
