  flex-direction: column;
  justify-content: space-around;
  gap: 2rem;
  border: 0.2rem solid #F0A500;
  position: relative;
}
.servicios__servicio::after {
  position: absolute;
  content: "";
  background-color: #F0A500;
  clip-path: circle(8.5% at 50% 100%);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  transition: all 0.5s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}
.servicios__servicio:hover::after {
  clip-path: circle(200% at 50% 100%);
}
.servicios__servicio:hover ul {
  color: #FFFFFF;
}
.servicios__servicio:hover img {
  filter: none;
  filter: invert(1);
}
.servicios__servicio__imagen {
  width: 50%;
  margin: 0 auto;
