@use "base/variables" as v;
@use "base/mixins" as m;

.servicios {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    gap: 2rem;

    @include m.tablet {
        flex-direction: row;
    }

    &__servicio {
        padding: 2rem;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        gap: 2rem;
        border: 0.2rem solid v.$amarillo;
        position: relative;

        &::after {
            position: absolute;
            content: "";
            background-color: v.$amarillo;
            clip-path: circle(8.5% at 50% 100%);
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: -1;
            transition: all 0.5s cubic-bezier(0.455, 0.03, 0.515, 0.955);
        }

        &:hover {
            &::after {
                clip-path: circle(200% at 50% 100%);
            }
            ul {
                color: v.$blanco;
            }

            img {
                filter: none;
                filter: invert(1);
            }
        }

        &__imagen {
            width: 50%;
            margin: 0 auto;
            transition: all 0.5s ease-in-out;
        }

        &__contenido {
            flex: 1;

            p {
                text-align: center;
                font-weight: v.$black;
                color: v.$azul;
                transition: all 0.5s ease-in-out;
            }

            &__lista {
                color: v.$negro;
                transition: all 0.5s ease-in-out;
                

                li{
                    margin-bottom: 1rem;
                    line-height: 1.5;
                }
            }
        }
    }
}

.servicios__contenido {
    padding-top: 2rem;

    &__section {
        &.nombre__servicio {
            margin-bottom: 0;
        }
    }
    .desarrollo-web {
        .desarrollo {
            .tu-pagina {
            }
        }
    }
}

.desarrollo__cuadro {
    display: flex;
    flex-direction: column;
    gap: 4rem;
    padding: 2rem;

    @include m.tablet {
        @include m.grid(3, 2rem);
        text-align: left;
    }

    .desarrollo__cuadro-web {
        border: 0.2rem solid v.$amarillo;
        transition: all 0.5s ease-in-out;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &:hover {
            transform: translateY(-2rem);
        }

        .cuadro__web-header {
            background-color: v.$amarillo;
            padding: 2rem;
            .nombre {
                color: v.$negro;
                margin: 0;
            }

            .precio {
                p {
                    font-size: 3vh;
                    font-weight: bold;
                    margin: 1rem 0;

                    @include m.tablet {
                        font-size: 4vh;
                    }
                }
            }
        }

        .cuadro__web-main {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: 100%;

            .lista__servicios {
                overflow: hidden;
                margin: 0;
                list-style: none;
                padding: 2rem;
                color: v.$blanco;

                li {
                    margin: 1rem 0;
                }
            }

            .boton {
                @include m.boton(v.$amarillo, v.$negro);
                background-color: v.$amarillo;
                width: 100%;
                border: none;
            }
        }
    }
}
