@use "base/variables" as v;
@use "base/mixins" as m;

.sobre-mi {
    display: flex;
    flex-direction: column;
    gap: 2rem;

    @include m.tablet {
        flex-direction: row;
    }

    .sobre-mi__izquierda {
        padding: 2rem;
        flex: 1;

        .nombre {
            h3 {
                text-align: left;
                margin: 0;
                font-size: 2.5rem;
            }
            span {
                display: block;
                color: v.$blanco;
                font-weight: bold;
            }
            img {
                width: 50%;
                margin: 2rem auto;
            }
        }

        .sobre-mi__contenido {
            .cuadro__sobre-mi {
                h3 {
                    text-align: left;
                    font-size: 2.5rem;
                    margin: 0;
                }

                p {
                    line-height: 2;
                }
            }

            .cuadro__contacto {
                h3 {
                    text-align: left;
                    font-size: 2.5rem;
                    margin: 0;
                }

                .lista__contacto {
                    all: unset;
                    list-style: none;

                    li {
                        .web {
                            color: v.$azul;
                        }
                    }
                }
            }

            .cuadro__skills {
                .habilidades {
                    margin: 0;
                    padding: 0;
                    width: 100%;
                    h3 {
                        font-size: 2.5rem;
                        text-align: left;
                    }

                    .contenido-habilidades {
                        width: 80%;
                    }
                }
            }
        }
    }

    .sobre-mi__derecha {
        padding: 2rem;
        flex: 1;

        .cuadro__experiencia {
            h3 {
                text-align: left;
                font-size: 2.5rem;
                margin: 0;
            }

            .experiencia {
                border-bottom: 0.2rem solid v.$amarillo;
                margin-bottom: 4rem;

                .nombre__empresa {
                    font-weight: bold;
                    display: block;
                    color: v.$azul;
                    margin-top: 2rem;
                }

                .fecha {
                    display: block;
                }

                .lista__actividades {
                    all: unset;
                    list-style: circle red;

                    li {
                        color: v.$blanco;
                    }
                }
            }
        }

        .cuadro__estudios {
            h3 {
                font-size: 2.5rem;
                text-align: left;
            }

            .estudio {
                border-bottom: 0.2rem solid v.$amarillo;
                margin-bottom: 4rem;

                &__titulo{
                    font-weight: v.$bold;
                    color: v.$azul;
                }
                span {
                    margin-bottom: 1rem;
                    display: block;
                }
            }
        }

        .cuadro__referencias {
            h3 {
                font-size: 2.5rem;
                text-align: left;
            }

            .contacto {
                border-bottom: 0.2rem solid v.$amarillo;
                margin-bottom: 4rem;

                .nombre {
                    font-weight: bold;
                    margin-bottom: 0;
                    color: v.$azul;
                }

                span {
                    display: block;
                }

                .cargo{
                    margin: .5rem 0;
                }

                .telefono {
                    margin-bottom: 1rem;
                }
            }
        }
    }
}
