@use "base/variables" as v;
@use "base/mixins" as m;

.formulario {
    width: 90%;
    padding: 2rem;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include m.tablet {
        width: 60%;
    }

    .campo {
        display: flex;
        flex-direction: column;
        margin-bottom: 2rem;
        color: v.$gris;

        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            padding: 1rem;
        }

        &:last-of-type {
            margin-bottom: 2rem;
        }
        label {
            font-weight: bold;
            color: v.$amarillo;
            flex: 1;
        }

        input {
            all: unset;
            flex: 2;
            border-bottom: 0.2rem solid v.$azul;
            padding: 1rem;
            color: v.$azul;

            &:focus {
                border-color: v.$amarillo;
            }
        }

        textarea {
            flex: 2;
            resize: none;
            padding: 1rem;
            border: none;
            border-bottom: 0.2rem solid v.$azul;
            background-color: transparent;
            color: v.$azul;

            &:focus {
                outline: none;
                border-color: v.$amarillo;
            }
        }
    }

    .boton {
        @include m.boton(v.$amarillo, v.$blanco);
        border: none;
        transition: transform .5s ease-in;

        @include m.tablet {
            padding: 1rem 3rem;
        }

        &:hover {
            transform: scale(1.1);}
    }
}
