@use "base/variables" as v;
@use "base/mixins" as m;

.dark {
    position: fixed;
    bottom: 2rem;
    right: 4rem;
    font-size: 2.6rem;
    color: v.$amarillo;
    transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1);
    z-index: 100;
    border: 0.2rem;
    border-radius: 0.5rem;
    padding: 0.4rem;
    background-color: v.$blanco;
    transition: transform 0.5s ease;

    &:hover {
        transform: scale(1.2);
        color: v.$amarillo;
        cursor: pointer;
    }
}

.ir-arriba {
    position: fixed;
    bottom: 7rem;
    right: 3.7rem;
    font-size: 3rem;
    transition: all 0.5s ease-in-out;
    background-color: v.$blanco;
    color: v.$amarillo;
    padding: 0.5rem;
    border-radius: 50%;
    opacity: 0;
    z-index: -1;
    transition: all 0.5s ease-in-out;

    &:hover {
        cursor: pointer;
        transform: translateY(-1rem);
    }

    &.mostrar {
        opacity: 1;
        z-index: 99;
    }
}