@use "base/variables" as v;
@use "base/mixins" as m;

.nosotros {
    width: 100%;
    margin: 2rem auto;
    display: flex;
    flex-direction: column;
    gap: 4rem;
    text-align: center;
    padding: 2rem;
    opacity: 0;
    transition: all 1s ease-in-out;

    &.mostrar {
        opacity: 1;
    }

    @include m.telefono {
        @include m.grid(2, 1fr);
    }

    @include m.tablet {
        @include m.grid(4, 2rem);
    }   

    .cuadro {
        position: relative;
        min-height: 20rem;
        padding: 2rem;
        border: .1rem solid v.$azul;
        z-index: 9;
        transition: all 0.5s ease-in-out;

        &:hover {
            transform: rotateZ(4deg);
            background-color: v.$amarillo;
            border-color: v.$amarillo;
            box-shadow: none;
            transform-origin: top left;

            img{
                filter: none;
            }
            h4 {
                color: v.$negro;
            }

            p {
                color: v.$blanco;
            }
        }
    }

    img {
        max-width: 50%;
        filter: invert(0.6);
        transition: all .5s ease-in-out;
        z-index: 10;

        @include m.tablet{
            max-width: 60%;
        }
    }

    h4 {
        margin-top: 2rem;
        transition: all .5s ease-in-out;
        z-index: 10;
    }

    p{
        margin: 0;
        position: absolute;
        bottom: 6rem;
        right: -4rem;
        font-size: 7rem;
        transform: rotate(270deg);
        font-weight: v.$black;
        color: rgba(v.$negro, .8);
        z-index: -1;
    }
}
