@use "base/variables" as v;
@use "base/mixins" as m;

.habilidades {
    padding: 4rem;
    display: flex;
    flex-direction: column;
    opacity: 0;
    transform: scale(0.5);
    transition: all 0.5s ease-in-out;

    &.mostrar {
        opacity: 1;
        transform: scale(1);
    }

    .contenido-habilidades {
        display: flex;
        flex-direction: column;
        gap: 2rem;

        @include m.tablet {
            @include m.grid(4, 2rem);
        }
        @include m.desktop {
            width: 80%;
            margin: auto;
            @include m.grid(5, 2rem);
        }

        .habilidad {
            position: relative;
            text-align: center;
            position: relative;
            min-height: 12rem;
            min-width: 15rem;
            margin: auto;
            border: 0.1rem solid v.$azul;
            border-radius: 0.5rem;
            transition: all 0.5s ease-in-out;

            &:hover {
                .titulo {
                    opacity: 0;
                    transition: opacity 0.5s ease-in-out;
                }

                .porcentaje {
                    opacity: 0;
                    transition: opacity 0.5s ease-in-out;
                }

                .descripcion {
                    opacity: 1;
                    transition: opacity 0.5s ease-in-out;
                }
            }

            .titulo {
                font-weight: v.$bold;
                text-transform: uppercase;
                transition: opacity 0.5s ease-in-out;
            }

            .porcentaje {
                font-size: 3rem;
                font-weight: v.$black;
                color: v.$amarillo;
                transition: opacity 0.5s ease-in-out;
            }

            .descripcion {
                position: absolute;
                margin: 0;
                padding: 1rem;
                top: 0;
                opacity: 0;
                transition: opacity 0.5s ease-in-out;
            }
        }
    }
}
